.ng2-c-selection-list {
  display: block; }

.ng2-c-selection-list__item {
  align-items: stretch;
  border-bottom: 1px solid #f5f5f5;
  box-sizing: border-box;
  color: #28648a;
  display: flex;
  font-size: 18px;
  font-weight: 400;
  height: 50px;
  overflow: hidden;
  white-space: nowrap; }
  .ng2-c-selection-list__item:last-child {
    border-bottom: none; }

.ng2-c-selection-list__label {
  flex-grow: 1;
  overflow: hidden; }

.ng2-c-selection-list__label-text {
  flex-grow: 1;
  overflow: hidden;
  text-overflow: ellipsis; }

.ng2-c-selection-list__toggle {
  display: inline-flex;
  justify-content: center;
  min-width: 50px; }

.ng2-c-selection-list--small .ng2-c-selection-list__item {
  height: 40px; }

.ng2-c-selection-list--center {
  text-align: center;
  /*
        Unset margin-right and compensate for space used by the checkbox
        by setting margin-left to the same width as the checkbox.
        Otherwise the label text is not centered in the list item.
    */ }
  .ng2-c-selection-list--center .ng2-c-selection-list__label-text {
    margin-left: 50px;
    margin-right: 0; }
